@font-face {
  font-family: "Lato";
  src: url("../fonts/latolight.woff2") format("woff2"), url("../fonts/latolight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/latoregular.woff2") format("woff2"), url("../fonts/latoregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/latoblack.woff2") format("woff2"), url("../fonts/latoblack.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/latoheavy.woff2") format("woff2"), url("../fonts/latoheavy.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
body {
  font-size: 16px;
  font-family: "Lato", Arial, sans-serif;
  font-weight: 300;
}
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
*,
*::after,
*::before {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  position: relative;
  max-width: 1270px;
  margin: 0 auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
/*base end*/
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 180px;
}
.header__phone {
  display: flex;
  align-items: center;
}
.header__phone-link {
  color: #666666;
  font-size: 20px;
  margin-left: 10px;
}
.button {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  color: #a59552;
  background-color: transparent;
  border: 1px solid #a59552;
  padding: 10px 20px;
  cursor: pointer;
  transition: 0.4s;
}
.button:hover {
  background-color: #0abbb5;
  border-color: #0abbb5;
  color: #ffffff;
}
.nav {
  margin-right: 100px;
  margin-bottom: 40px;
  margin-left: 100px;
}
.nav__list {
  list-style-type: none;
  padding-left: 0;
  display: flex;
  justify-content: space-between;
}
.nav__link {
  text-transform: uppercase;
  color: #333333;
  font-size: 18px;
  padding: 3px 5px;
  border-bottom: 1px solid transparent;
  transition: 0.6s;
}
.nav__link:hover {
  color: #a59552;
  border-bottom: 1px solid #0abbb5;
}
.nav__link--active {
  color: #a59552;
  border-bottom: 1px solid #0abbb5;
}
.nav__container {
  position: relative;
}
.nav__container:hover > .nav__services {
  display: block;
}
.nav__services {
  list-style-type: none;
  background: #ffffff;
  width: 330px;
  padding-bottom: 10px;
  padding-left: 30px;
  font-size: 16px;
  position: absolute;
  top: 24px;
  left: -25px;
  z-index: 5;
  display: none;
}
.nav__servicesLink {
  display: block;
  line-height: 24px;
  padding: 10px 0;
  color: #333333;
  transition: 0.4s;
}
.nav__servicesLink:hover {
  color: #0abbb5;
}
.slider {
  position: relative;
  background: rgba(129, 216, 208, 0.46);
  margin-bottom: 110px;
  font-size: 0;
}
.slide {
  display: flex;
  align-items: center;
  justify-content: center;
}
.slideNext {
  display: none;
}
.slide__nav {
  padding: 40px;
  cursor: pointer;
  background: inherit;
  border: none;
  transition: 0.3s;
}
.slide__nav:hover {
  background: #81d8d0;
}
.slide__nav--forward {
  transform: scale(-1, 1);
}
.slider__controls {
  position: absolute;
  top: 490px;
  left: 50%;
  margin-left: -100px;
  display: flex;
}
.slider__controlsItem {
  width: 20px;
  height: 20px;
  border: 2px solid #0abbb5;
  border-radius: 50%;
  margin: 0 10px;
}
.slider__controlsItem--active {
  background: #0abbb5;
  transition: 0.3s;
}
.mainText {
  color: #333333;
  font-size: 18px;
  line-height: 28px;
  max-width: 750px;
  margin: 0 auto;
}
.sections {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 120px;
  margin-bottom: 80px;
  font-size: 0;
}
.sections__item {
  position: relative;
}
.sections__link {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  width: 400px;
  height: 280px;
  transition: 0.4s;
}
.sections__link:hover {
  background: rgba(129, 216, 208, 0.85);
}
.sections__link-text {
  font-size: 25px;
  font-weight: 800;
  text-transform: uppercase;
  margin-left: 10px;
}
.service__item {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}
.service__img {
  position: relative;
  background: rgba(129, 216, 208, 0.46);
  padding: 0 15px;
  font-size: 0;
}
.service__img::after {
  content: '';
  display: block;
  background: rgba(129, 216, 208, 0.46);
  width: 50vw;
  height: 210px;
  position: absolute;
  top: 0;
  right: 100%;
}
.service__img--right {
  order: 1;
}
.service__img--right::after {
  right: 0;
  left: 100%;
}
.service__name {
  margin-left: 50px;
  width: 380px;
}
.service__name--left {
  margin-left: 0;
  margin-right: 50px;
  text-align: right;
}
.service__head {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 30px;
  text-transform: uppercase;
}
.service__text {
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 50px;
}
.footer {
  background: #666666;
}
.footer__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  padding: 20px 0;
  color: #ffffff;
  font-size: 20px;
  font-weight: 400;
}
.footer__socialLink a {
  margin-right: 10px;
}
.footer__contactsItem {
  display: flex;
  align-items: center;
  height: 35px;
}
.footer__contactsIcon {
  margin-right: 10px;
}
.footer__contacts p {
  line-height: 14px;
}
.footer__contacts a {
  color: #ffffff;
}
