.footer {
  background: #666666;
}

.footer__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  padding: 20px 0;
  color: #ffffff;
  font-size: 20px;
  font-weight: 400;
}

.footer__socialLink a {
  margin-right: 10px;
}

.footer__contactsItem {
  display: flex;
  align-items: center;
  height: 35px;
}

.footer__contactsIcon {
  margin-right: 10px;
}

.footer__contacts p {
  line-height: 14px;
}

.footer__contacts a {
  color: #ffffff;
}
