.slider {
  position: relative;
  background: @colorGreenLight;
  margin-bottom: 110px;
  font-size: 0;
}

.slide {
  display: flex;
  align-items: center;
  justify-content: center;
}

.slideNext {
  display: none;
}

.slide__nav {
  padding: 40px;
  cursor: pointer;
  background: inherit;
  border: none;
  transition: 0.3s;

  &:hover {
    background: rgb(129, 216, 208);
  }
}

.slide__nav--forward {
  transform: scale(-1, 1);
}

.slider__controls {
  position: absolute;
  top: 490px;
  left: 50%;
  margin-left: -100px;
  display: flex;
}

.slider__controlsItem {
  width: 20px;
  height: 20px;
  border: 2px solid @colorGreen;
  border-radius: 50%;
  margin: 0 10px;
}

.slider__controlsItem--active {
  background: @colorGreen;
  transition: 0.3s;
}
