.nav {
  margin-right: 100px;
  margin-bottom: 40px;
  margin-left: 100px;
}

.nav__list {
  list-style-type: none;
  padding-left: 0;
  display: flex;
  justify-content: space-between;
}

.nav__link {
  text-transform: uppercase;
  color: @colorText;
  font-size: 18px;
  padding: 3px 5px;
  border-bottom: 1px solid transparent;
  transition: 0.6s;

  &:hover {
    color: #a59552;
    border-bottom: 1px solid @colorGreen;
  }
}

.nav__link--active {
  color: #a59552;
  border-bottom: 1px solid @colorGreen;
}

.nav__container {
  position: relative;

  &:hover > .nav__services {
    display: block;
  }
}

.nav__services {
  list-style-type: none;
  background: #ffffff;
  width: 330px;
  padding-bottom: 10px;
  padding-left: 30px;
  font-size: 16px;
  position: absolute;
  top: 24px;
  left: -25px;
  z-index: 5;
  display: none;
}

.nav__servicesLink {
  display: block;
  line-height: 24px;
  padding: 10px 0;
  color: @colorText;
  transition: 0.4s;

  &:hover {
    color: @colorGreen;
  }
}
