.button {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  color: #a59552;
  background-color: transparent;
  border: 1px solid #a59552;
  padding: 10px 20px;
  cursor: pointer;
  transition: 0.4s;

  &:hover {
    background-color: @colorGreen;
    border-color: @colorGreen;
    color: #ffffff;
  }
}
