.sections {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 120px;
  margin-bottom: 80px;
  font-size: 0;
}

.sections__item {
  position: relative;
}

.sections__link {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  width: 400px;
  height: 280px;
  transition: 0.4s;

  &:hover {
    background: rgba(129, 216, 208, 0.85);
  }
}

.sections__link-text {
  font-size: 25px;
  font-weight: 800;
  text-transform: uppercase;
  margin-left: 10px;
}
