.service__item {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.service__img {
  position: relative;
  background: @colorGreenLight;
  padding: 0 15px;
  font-size: 0;

  &::after {
    content: '';
    display: block;
    background: @colorGreenLight;
    width: 50vw;
    height: 210px;
    position: absolute;
    top: 0;
    right: 100%;
  }
}

.service__img--right {
  order: 1;

  &::after {
    right: 0;
    left: 100%;
  }
}

.service__name {
  margin-left: 50px;
  width: 380px;
}

.service__name--left {
  margin-left: 0;
  margin-right: 50px;
  text-align: right;
}

.service__head {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 30px;
  text-transform: uppercase;
}

.service__text {
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 50px;
}
